import { useCallback, useReducer } from "react";

const formReducer = (state, action) => {
  switch (action.type) {
    case "INPUT_CHANGE":
      let formIsValid = true;
      for (const titleId in state.inputs) {
        if (!state.inputs[titleId]) {
          continue;
        } else if (titleId === action.titleId) {
          formIsValid = formIsValid && action.isValid;
        } else {
          formIsValid = formIsValid && state.inputs[titleId].isValid;
        }
      }
      return {
        ...state,
        inputs: {
          ...state.inputs,
          [action.titleId]: { value: action.value, isValid: action.isValid },
        },

        isValid: formIsValid,
      };

    case "SET_DATA":
      return {
        inputs: action.setInputs,
        isValid: action.setValidity,
      };
    default:
      return state;
  }
};

export const useForm = (initialInputs, initialValidation) => {
  const [formState, dispatch] = useReducer(formReducer, {
    inputs: initialInputs,
    isValid: initialValidation,
  });

  const inputHandler = useCallback((id, value, isValid) => {
    dispatch({
      type: "INPUT_CHANGE",
      titleId: id,
      value: value,
      isValid: isValid,
    });
  }, []);

  const setFormData = useCallback((inputsData, formValidity) => {
    dispatch({
      type: "SET_DATA",
      setInputs: inputsData,
      setValidity: formValidity,
    });
  }, []);

  return [formState, inputHandler, setFormData];
};
